<template>
  <div class="user-wrap">
    <van-nav-bar
      title="寄售"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-field
      readonly
      clickable
      label="一级分类"
      :value="type1"
      placeholder="选择一级分类"
      @click="showPicker1 = true"
      input-align="right"
    />
    <van-field
      readonly
      clickable
      label="二级分类"
      :value="type2"
      placeholder="选择二级分类"
      @click="showPicker2 = true"
      input-align="right"
    />
    <van-field
      readonly
      clickable
      label="商品"
      :value="type3"
      placeholder="选择商品"
      @click="showPicker3 = true"
      input-align="right"
    />
    <van-field 
      label="价格"
      v-model="price" 
      error
      disabled 
      input-align="right"
    />
    <van-notice-bar :scrollable="false">
      <p class="small-word">
        当前可寄售数量: {{canSell}}
      </p>
    </van-notice-bar>
    <van-divider></van-divider>
    <div class="quan-contain" v-if="upType.indexOf(1) >= 0">
      <van-field
        v-model="quanma"
        rows="4"
        autosize
        label=""
        type="textarea"
        maxlength="500"
        placeholder="上传数字码/链接，多个券码用回车键分开"
        clearable
        show-word-limit
        input-align="left"
      />
    </div>
    <div class="quan-contain" v-if="upType.indexOf(2) >= 0">
      <van-field name="uploader" label="">
        <template #input>
          <van-uploader v-model="uploader" style="margin-left: 40%;"/>
        </template>
      </van-field>
      <van-notice-bar>
        <p class="small-word">上传券码截屏/压缩包，要求图片有完整条码或二维码</p>
      </van-notice-bar>
    </div>
    <van-field
      v-model="source"
      label=""
      rows="2"
      autosize
      type="textarea"
      maxlength="100"
      placeholder="3天内到期券码注明有效期、非指定活动券码注明来源"
      clearable
      show-word-limit
      input-align="left"
    />
    <div class="tips">
      <p class="title small-word">来源及提示</p>
      <p class="small-word">来源</p>
      <div class="content">
        <p class="small-word">
          *招行掌上生活5元话费、8元星巴克卷，叠加随机立减2元以上，买美式
        </p>
        <p class="small-word">
          https://market.cmbchina.com/ccard/wap/BBXFXY/index.html
        </p>
        <p class="small-word">
          *邮储信用卡app，生活，积分商城，搜索星巴克
        </p>
        <p class="small-word">
          *8邮储信用卡app，E商城“悦享咖啡10分满意”，每月可兑换3张30元星巴克代金券
        </p>
        <p class="small-word">
          *24点支付宝口碑定位贵阳-右上角人头-我的 -我的银行卡特惠- 
        </p>
        <p class="small-word">
          建行信用卡领卷-领1张50卷+1张15卷，搜星巴克买拿铁减15实付17。
        </p>
        <p class="small-word">
          1人一周两次，一月4次领过的还可以领。
        </p>
      </div>
      <p class="small-word">提示：</p>
      <div class="content">
        <p class="small-word">
          *只收一手券，核实不到券码的使用情况的提供不了凭证的有纠纷自行承当。
        </p>
        <p class="small-word">
          *请确保券码有效期大于10天。
        </p>
        <p class="small-word">
          *若上传的串码已被使用或为错误串码，即便您已拿到货款，后续也会被追责，故请卖家认真核实。
        </p>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="goPage('/qscode', 1)" />
      <van-goods-action-icon icon="star-o" text="收藏" />
      <van-goods-action-button type="danger" text="提交" color="#07c160" @click="onSubmit"/>
    </van-goods-action>
    <van-popup v-model="showPicker1" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns1"
        @cancel="showPicker1 = false"
        @confirm="onConfirm1"
      />
    </van-popup>
    <van-popup v-model="showPicker2" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns2"
        @cancel="showPicker2 = false"
        @confirm="onConfirm2"
      />
    </van-popup>
    <van-popup v-model="showPicker3" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns3"
        @cancel="showPicker3 = false"
        @confirm="onConfirm3"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'parchase',
  components: {
  },
  data () {
    return {
      upType: [1,2],
      type1: '',
      type2: '',
      type3: '',
      price: '¥2131',
      canSell: 32,
      source: '',
      active: 0,
      showPicker1: false,
      showPicker2: false,
      showPicker3: false,
      columns1: ['1a', '1b', '1c', '1d', '1e'],
      columns2: ['2a', '2b', '2c', '2d', '2e'],
      columns3: ['3a', '3b', '3c', '3d', '3e'],
      quanma: '',
      uploader: []
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onConfirm1(value) {
      this.type1 = value
      this.showPicker1 = false
    },
    onConfirm2(value) {
      this.type2 = value
      this.showPicker2 = false
    },
    onConfirm3(value) {
      this.type3 = value
      this.showPicker3 = false
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    onSubmit () {
      console.log('submit')
    },
    goPage (url, type) {
      this.$router.push({ path: url, query: {type: type} })
    }
  }
}
</script>

<style scoped lang="less">
  .quan-contain {
    padding: 10px 0;
  }

  .tips {
    padding: 10px 15px;
    color: #666;
    
    .content {
      // max-height: 100px;
      // overflow-y: auto;
      margin-bottom: 10px;
    }

    p {
      &.title {
        color: #333;
        line-height: 14px;
        padding-left: 6px;
        border-left: 3px solid red;
        margin-bottom: 10px;
      }

      line-height: 20px;
    }
  }
</style>


